import {DISPLAY_SHOW, GET_GENRES} from "./typeAction";


const initialState = {
    genres: [],
    countries: [],
    displaySpoiler: false
}

export const filterReduser = (state = initialState, action) => {
    switch (action.type) {
        case GET_GENRES:
            return {
                ...state,
                genres: action.genres,
                countries: action.countries
            }
        case DISPLAY_SHOW:
            return {
                ...state,
                displaySpoiler: !action.status

            }
        default:
            return state
    }
}
