import React from "react";
import filterStyle from './Filter.module.css'
import Filter from "./Filter";
import {StyleMobile} from "../utils/StyleMob";
import {useDispatch, useSelector} from "react-redux";
import {darkThemeSelector} from "../../Redux/selectors";
import {setSpoilerShow} from "../../Redux/actions";

export const FilterMob = () => {

   const displaySpoiler = useSelector(state => state.filter.displaySpoiler)
   const dispatch = useDispatch()
   const darkTheme = useSelector(darkThemeSelector);

   return (
     <div className={filterStyle.spoiler}>
        <div className={filterStyle.spoiler__item}>
           <h2
             className={StyleMobile(filterStyle, filterStyle.spoiler__title, displaySpoiler, darkTheme)}
             onClick={()=>dispatch(setSpoilerShow(displaySpoiler))}>
              Фильтр
           </h2>
           <div className={displaySpoiler ? filterStyle.spoiler__body + ' ' + filterStyle.active : filterStyle.spoiler__body}>
              <Filter />
           </div>
         </div>
     </div>

   )
}